import React, { Component } from 'react'
import { Modal } from 'antd'
import ModalForm from './DetailModalStat'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

export default class ModalComponent extends Component {
  state = {
    loading: false
  }

  render() {
    const visible = this.props.visible
    const onCancelClick = this.props.cancel
    const dataStatus = this.props.data ? this.props.data.status : 1

    return (
      <Modal
        title="/"
        visible={visible}
        width="90%"
        onCancel={onCancelClick}
        destroyOnClose={true}
        footer={null}
      >
        {/* Status */}

        <div className="d-flex justify-content-end">
          {dataStatus === 1 ? (
            <div className="alert alert-warning px-3 py-2">
              <h4 className="mb-0">
                <FontAwesomeIcon icon="clock" /> 
              </h4>
            </div>
          ) : dataStatus === 3 ? (
            <div className="alert alert-danger px-3 py-2">
              <h4 className="mb-0">
                <FontAwesomeIcon icon="exclamation-triangle" /> 
              </h4>
            </div>
          ) : dataStatus === 2 ? (
            <div className="alert alert-success px-3 py-2">
              <h4 className="mb-0">
                <FontAwesomeIcon icon="check" /> 
              </h4>
            </div>
          ) : (
            <div className="alert alert-info px-3 py-2">
              <h4 className="mb-0">
                <FontAwesomeIcon icon="file-alt" /> 
              </h4>
            </div>
          )}
        </div>

        <ModalForm
          dataFromTable={this.props.data}
          handleOk={this.props.clickOk}
          handleApprove={this.props.clickApprove}
          handleSendback={this.props.clickSendback}
          loadingOk={this.props.loadingOk}
          loadingSendback={this.props.loadingSendback}
          loadingApprove={this.props.loadingApprove}
          disCancel={this.props.isDisCancel}
          onCancelClick={onCancelClick}
          resfilterAirport={this.props.filterAirport}
        />
      </Modal>
    )
  }
}
