import React, { Component, Fragment } from 'react'
import {
  Button,
  Input,
  Popover,
  DatePicker,
  Radio,
  Select,
  Upload,
  message,
  Icon
} from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Formik } from 'formik'
import TextArea from 'antd/lib/input/TextArea'
import moment from 'moment'

const RadioGroup = Radio.Group
const Option = Select.Option
const props = {
  name: 'file',
  action: '//jsonplaceholder.typicode.com/posts/',
  headers: {
    authorization: 'authorization-text'
  },
  onChange(info) {
    if (info.file.status !== 'uploading') {
      console.log(info.file, info.fileList)
    }
    if (info.file.status === 'done') {
      message.success(`${info.file.name} file uploaded successfully`)
    } else if (info.file.status === 'error') {
      message.error(`${info.file.name} file upload failed.`)
    }
  }
}

export default class ModalFlightStat extends Component {
  state = {
    visible: false,
    title: '',
    content: '',
    getDateString: '',
    btnSubmit: false,
    btnApprove: false,
    btnSendback: false,
    value: 1,
    radioNot: true,
    radio1: false,
    radio2: false,
    showSelect: false,
    setPortNameICAO: this.props.dataFromTable.portNameICAO,
    checkPortNameICAO: '',
    setTrnDate: this.props.dataFromTable.trnDate,
    checkTrnDate: '',
    setTrnDay: this.props.dataFromTable.trnDay,
    checkTrnDay: '',
    setAirline: this.props.dataFromTable.airline,
    checkAirline: '',
    setCallsign: this.props.dataFromTable.callsign,
    checkCallsign: '',
    setAirtype: this.props.dataFromTable.airType,
    checkAirtype: '',
    setArrDep: this.props.dataFromTable.arr_dep,
    checkArrDep: '',
    setTrafftype: this.props.dataFromTable.traffType,
    checkTrafftype: '',
    setPortType: this.props.dataFromTable.portType,
    checkPortType: '',
    selectPitParking: 'd-none'
  }

  onRadio1Click = e => {
    this.setState({
      value: e.target.value,
      radio1: true,
      radio2: false,
      radioNot: false,
      showSelect: true
    })
  }

  onRadio2Click = e => {
    this.setState({
      value: e.target.value,
      radio2: true,
      radio1: false,
      radioNot: false,
      showSelect: true
    })
  }

  onRadioNotClick = e => {
    this.setState({
      value: e.target.value,
      radio2: false,
      radio1: false,
      radioNot: true,
      showSelect: false
    })
  }

  onChange = e => {
    console.log('radio checked', e.target.value)
    e.target.value === 1
      ? this.setState({
          value: e.target.value,
          selectPitParking: 'd-none'
        })
      : this.setState({
          value: e.target.value,
          selectPitParking: 'd-block'
        })
  }

  setChangeData = (props, value, nameStateCheck, nameStateSet) => {
    if (props !== value) {
      this.setState({ [nameStateCheck]: props })
    }
    this.setState({ [nameStateSet]: value })
  }

  setData = (props, value, nameStateSet) => {
    if (props !== value) {
      return props
    } else {
      return this.state[nameStateSet]
    }
  }

  setTriggerBtnSubmit = () => {
    this.setState({ btnSubmit: true, btnApprove: false, btnSendback: false })
  }

  setTriggerBtnApprove = () => {
    this.setState({ btnSubmit: false, btnApprove: true, btnSendback: false })
  }

  setTriggerBtnSendback = () => {
    this.setState({ btnSubmit: false, btnApprove: false, btnSendback: true })
  }

  render() {
    const data = this.props.dataFromTable
    const { btnSubmit, btnApprove } = this.state

    return (
      <Fragment>
        <Formik
          enableReinitialize
          initialValues={{
            operator: data.operaterStat,
            recNo: data.recNo,
            trnTime: data.trnTime,
            flightNo: data.flightNo,
            scheduleTime: data.scheduleTime,
            portNameIATA: data.portNameIATA,
            cityICAO: data.cityICAO,
            cityIATA: data.cityIATA,
            cardNo: data.cardNo,
            airportRemark: data.airportRemark,
            zoneNo: data.zoneNo,
            regionNO: data.regionNo,
            flightStatus: data.flightStatusTxt,
            flightTime: data.flightStatusTime,
            cause: data.flightStatusReason,
            flightNote: data.flightStatusRemark,
            passenger: data.passenger_total,
            passengerDomestic: data.passenger_dom,
            passengerInternation: data.passenger_inter,
            infants: data.passenger_infants,
            passengerTransfer: data.passenger_transfer,
            passengerTransit: data.passenger_transit,
            freight: data.freight_total,
            freightDomestic: data.freight_dom,
            freightInternation: data.freight_inter,
            freightTransfer: data.freight_transfer,
            freightTransit: data.freight_transit,
            mail: data.freight_mail,
            remark: data.remark,
            portNameICAO: this.state.setPortNameICAO
          }}
          onSubmit={values =>
            btnSubmit
              ? this.props.handleOk(
                  values,
                  '',
                  ''
                )
              : btnApprove
              ? this.props.handleApprove(values)
              : this.props.handleSendback(values)
          }
          render={({
            values,
            setFieldValue,
            errors,
            touched,
            handleChange,
            handleBlur,
            handleSubmit,
            handleReset
          }) => (
            <form onSubmit={handleSubmit}>
              {/*  */}
              <h4 className="mb-4"></h4>
              <div className="form-row mb-4">
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Operator :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      addonAfter={
                        <Popover content="Test" title="Title">
                          <FontAwesomeIcon icon="info" />
                        </Popover>
                      }
                      name="operator"
                      placeholder=" Operator"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.operator}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Rec No :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="recNo"
                      type="text"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.recNo}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Port Name (ICAO) :
                  </label>
                  <div className="col col-sm-6">
                    <Select
                      name="portNameICAO"
                      showSearch
                      style={{ width: '100%' }}
                      onChange={value => (
                        this.setChangeData(
                          data.portNameICAO,
                          value,
                          'checkPortNameICAO',
                          'setPortNameICAO'
                        )(values.portNameICAO = value)
                      )}
                      placeholder=""
                      optionFilterProp="children"
                      onBlur={handleBlur}
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                      value={this.setData(
                        data.portNameICAO,
                        this.state.checkPortNameICAO,
                        'setPortNameICAO'
                      )}
                    >
                      {this.props.resfilterAirport.map(val => (
                        <Option key={val.airportID} value={val.airportID}>
                          {val.airportCodeICAO != null
                            ? val.airportCodeICAO
                            : ''}
                        </Option>
                      ))}
                    </Select>
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    TRN Date :
                  </label>
                  <div className="col col-sm-6">
                    <DatePicker
                      style={{ width: '100%' }}
                      name="trnDate"
                      placeholder=""
                      onChange={(date, dateString) => {
                        this.setChangeData(
                          data.trnDate,
                          dateString,
                          'checkTrnDate',
                          'setTrnDate'
                        )
                        values.trnDate = dateString
                      }}
                      value={moment(
                        this.setData(
                          data.trnDate,
                          this.state.checkTrnDate,
                          'setTrnDate'
                        ),
                        'YYYYMMDD'
                      )}
                    />
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    TRN Time :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="trnTime"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.trnTime}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    TRN Day :
                  </label>
                  <div className="col col-sm-6">
                    <Select
                      name="trnDay"
                      showSearch
                      style={{ width: '100%' }}
                      placeholder=""
                      optionFilterProp="children"
                      onChange={value =>
                        this.setChangeData(
                          data.trnDay,
                          value,
                          'checkTrnDay',
                          'setTrnDay'
                        )
                      }
                      onBlur={handleBlur}
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                      value={this.setData(
                        data.trnDay,
                        this.state.checkTrnDay,
                        'setTrnDay'
                      )}
                    >
                      <Option value="1"> (1)</Option>
                      <Option value="2"> (2)</Option>
                      <Option value="3"> (3)</Option>
                      <Option value="4"> (4)</Option>
                      <Option value="5"> (5)</Option>
                      <Option value="6"> (6)</Option>
                      <Option value="7"> (7)</Option>
                    </Select>
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Airline :
                  </label>
                  <div className="col col-sm-6">
                    <Select
                      name="selectAirline"
                      showSearch
                      style={{ width: '100%' }}
                      placeholder=""
                      optionFilterProp="children"
                      onChange={value =>
                        this.setChangeData(
                          data.airline,
                          value,
                          'checkAirline',
                          'setAirline'
                        )
                      }
                      onBlur={handleBlur}
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                      value={this.setData(
                        data.airline,
                        this.state.checkAirline,
                        'setAirline'
                      )}
                    >
                      <Option value="CMI">CMI</Option>
                      <Option value="ATT">ATT</Option>
                      <Option value="DD">DD</Option>
                      <Option value="SIA">SIA</Option>
                    </Select>
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Callsign :
                  </label>
                  <div className="col col-sm-6">
                    <Select
                      name="selectCallsign"
                      showSearch
                      style={{ width: '100%' }}
                      placeholder=""
                      optionFilterProp="children"
                      onChange={value =>
                        this.setChangeData(
                          data.callsign,
                          value,
                          'checkCallsign',
                          'setCallsign'
                        )
                      }
                      onBlur={handleBlur}
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                      value={this.setData(
                        data.callsign,
                        this.state.checkCallsign,
                        'setCallsign'
                      )}
                    >
                      <Option value="GLA-644">GLA-644</Option>
                      <Option value="HS-LUW">HS-LUW</Option>
                      <Option value="B-1610">B-1610</Option>
                      <Option value="SNY-15">SNY-15</Option>
                    </Select>
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Airtype :
                  </label>
                  <div className="col col-sm-6">
                    <Select
                      name="selectAirType"
                      showSearch
                      style={{ width: '100%' }}
                      placeholder=""
                      optionFilterProp="children"
                      onChange={value =>
                        this.setChangeData(
                          data.airType,
                          value,
                          'checkAirtype',
                          'setAirtype'
                        )
                      }
                      onBlur={handleBlur}
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                      value={this.setData(
                        data.airType,
                        this.state.checkAirtype,
                        'setAirtype'
                      )}
                    >
                      <Option value="EC-45">EC-45</Option>
                      <Option value="B738">B738</Option>
                      <Option value="A320">A320</Option>
                      <Option value="DA-42">DA-42</Option>
                    </Select>
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Flight No :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="flightNo"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.flightNo}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Schedule Time :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="scheduleTime"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.scheduleTime}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    ARR/DEP :
                  </label>
                  <div className="col col-sm-6">
                    <Select
                      name="selectARRDEP"
                      showSearch
                      style={{ width: '100%' }}
                      placeholder=""
                      optionFilterProp="children"
                      onChange={value =>
                        this.setChangeData(
                          data.arr_dep,
                          value,
                          'checkArrDep',
                          'setArrDep'
                        )
                      }
                      onBlur={handleBlur}
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                      value={this.setData(
                        data.arr_dep,
                        this.state.checkArrDep,
                        'setArrDep'
                      )}
                    >
                      <Option value="A">Arrival</Option>
                      <Option value="D">Departure</Option>
                    </Select>
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Traff Type :
                  </label>
                  <div className="col col-sm-6">
                    <Select
                      name="selectTraffType"
                      showSearch
                      style={{ width: '100%' }}
                      placeholder=""
                      optionFilterProp="children"
                      onChange={value =>
                        this.setChangeData(
                          data.traffType,
                          value,
                          'checkTrafftype',
                          'setTrafftype'
                        )
                      }
                      onBlur={handleBlur}
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                      value={this.setData(
                        data.traffType,
                        this.state.checkTrafftype,
                        'setTrafftype'
                      )}
                    >
                      <Option value="A">A</Option>
                      <Option value="B">B</Option>
                      <Option value="C">C</Option>
                      <Option value="D">D</Option>
                      <Option value="E">E</Option>
                      <Option value="F">F</Option>
                      <Option value="G">G</Option>
                    </Select>
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Port Type :
                  </label>
                  <div className="col col-sm-6">
                    <Select
                      name="selectPortType"
                      showSearch
                      style={{ width: '100%' }}
                      placeholder=""
                      optionFilterProp="children"
                      onChange={value =>
                        this.setChangeData(
                          data.portType,
                          value,
                          'checkPortType',
                          'setPortType'
                        )
                      }
                      onBlur={handleBlur}
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                      value={this.setData(
                        data.portType,
                        this.state.checkPortType,
                        'setPortType'
                      )}
                    >
                      <Option value="A">A</Option>
                      <Option value="B">B</Option>
                      <Option value="C">C</Option>
                      <Option value="D">D</Option>
                      <Option value="E">E</Option>
                      <Option value="F">F</Option>
                      <Option value="G">G</Option>
                    </Select>
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Port Name (IATA) :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="portNameIATA"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.portNameIATA}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    City (ICAO) :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="cityICAO"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.cityICAO}
                      style={{ width: '10d0%' }}
                    />
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    City (IATA) :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="cityIATA"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.cityIATA}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Card No. :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="cardNo"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.cardNo}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
                <div className="col-12 col-md-12 col-xl-12 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 col-xl-2 justify-content-end col-form-label">
                    Airport Remark :
                  </label>
                  <div className="col col-sm-6 col-xl-10 pr-xl-4">
                    <TextArea
                      rows="3"
                      name="airportRemark"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.airportRemark}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Zone No :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="zoneNo"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.zoneNo}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Region No :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="regionNO"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.regionNO}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
                <div className="w-100" />
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Flight status :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="flightStatus"
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.flightStatus}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                    Time :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="flightTime"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.flightTime}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
                <div className="w-100" />
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                     :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="cause"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.cause}
                    />
                  </div>
                </div>
                <div className="w-100" />
                <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 justify-content-end col-form-label">
                     :
                  </label>
                  <div className="col col-sm-6">
                    <Input
                      name="flightNote"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.flightNote}
                    />
                  </div>
                </div>
                <div className="w-100" />
                <div className="col-12 col-md-6 col-xl-8 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 col-xl-3 justify-content-end col-form-label">
                     :
                  </label>
                  <div className="col col-sm col-xl">
                    <RadioGroup
                      onChange={this.onChange}
                      value={this.state.value}
                    >
                      <Radio value={1}></Radio>
                      <Radio value={2}></Radio>
                      <Radio value={3}></Radio>
                    </RadioGroup>
                  </div>
                  <div className="col col-sm col-xl">
                    <Select
                      name="selectPitParking"
                      style={{ width: '100%' }}
                      placeholder=""
                      optionFilterProp="children"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      filterOption={(input, option) =>
                        option.props.children
                          .toLowerCase()
                          .indexOf(input.toLowerCase()) >= 0
                      }
                      className={this.state.selectPitParking}
                    >
                      <Option value="A">A</Option>
                      <Option value="B">B</Option>
                      <Option value="C">C</Option>
                      <Option value="D">D</Option>
                      <Option value="E">E</Option>
                      <Option value="F">F</Option>
                      <Option value="G">G</Option>
                    </Select>
                  </div>
                </div>
              </div>

              {/*  */}
              <h4 className="mb-4"></h4>
              <div className="card card-body bg-info-light mb-4">
                <div className="form-row">
                  <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                    <label className="col-12 col-sm-6 justify-content-end col-form-label">
                      Passenger :
                    </label>
                    <div className="col col-sm-6">
                      <Input
                        name="passenger"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.passenger}
                        style={{ width: '100%' }}
                      />
                    </div>
                  </div>
                  <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                    <label className="col-12 col-sm-6 justify-content-end col-form-label">
                      Passenger Domestic :
                    </label>
                    <div className="col col-sm-6">
                      <Input
                        name="passengerDomestic"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.passengerDomestic}
                        style={{ width: '100%' }}
                      />
                    </div>
                  </div>
                  <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                    <label className="col-12 col-sm-6 justify-content-end col-form-label">
                      Passenger Internation :
                    </label>
                    <div className="col col-sm-6">
                      <Input
                        name="passengerInternation"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.passengerInternation}
                        style={{ width: '100%' }}
                      />
                    </div>
                  </div>
                  <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                    <label className="col-12 col-sm-6 justify-content-end col-form-label">
                      Infants :
                    </label>
                    <div className="col col-sm-6">
                      <Input
                        name="infants"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.infants}
                        style={{ width: '100%' }}
                      />
                    </div>
                  </div>
                  <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                    <label className="col-12 col-sm-6 justify-content-end col-form-label">
                      Passenger Transfer :
                    </label>
                    <div className="col col-sm-6">
                      <Input
                        name="passengerTransfer"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.passengerTransfer}
                        style={{ width: '100%' }}
                      />
                    </div>
                  </div>
                  <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                    <label className="col-12 col-sm-6 justify-content-end col-form-label">
                      Passenger Transit :
                    </label>
                    <div className="col col-sm-6">
                      <Input
                        name="passengerTransit"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.passengerTransit}
                        style={{ width: '100%' }}
                      />
                    </div>
                  </div>
                </div>
              </div>

              {/*  /  */}
              <h4 className="mb-4"> / </h4>
              <div className="card card-body bg-primary-light mb-4">
                <div className="form-row">
                  <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                    <label className="col-12 col-sm-6 justify-content-end col-form-label">
                      Freight (kg.) :
                    </label>
                    <div className="col col-sm-6">
                      <Input
                        name="freight"
                        onBlur={handleBlur}
                        onChange={handleChange}
                        value={values.freight}
                        style={{ width: '100%' }}
                      />
                    </div>
                  </div>
                  <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                    <label className="col-12 col-sm-6 justify-content-end col-form-label">
                      Freight Domestic (kg.) :
                    </label>
                    <div className="col col-sm-6">
                      <Input
                        name="freightDomestic"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.freightDomestic}
                        style={{ width: '100%' }}
                      />
                    </div>
                  </div>
                  <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                    <label className="col-12 col-sm-6 justify-content-end col-form-label">
                      Freight Internation (kg.) :
                    </label>
                    <div className="col col-sm-6">
                      <Input
                        name="freightInternation"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.freightInternation}
                        style={{ width: '100%' }}
                      />
                    </div>
                  </div>
                  <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2" />
                  <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                    <label className="col-12 col-sm-6 justify-content-end col-form-label">
                      Freight Transfer (kg.) :
                    </label>
                    <div className="col col-sm-6">
                      <Input
                        name="freightTransfer"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.freightTransfer}
                        style={{ width: '100%' }}
                      />
                    </div>
                  </div>
                  <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                    <label className="col-12 col-sm-6 justify-content-end col-form-label">
                      Freight Transit (kg.) :
                    </label>
                    <div className="col col-sm-6">
                      <Input
                        name="freightTransit"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.freightTransit}
                        style={{ width: '100%' }}
                      />
                    </div>
                  </div>
                </div>
              </div>

              {/*  */}
              <h4 className="mb-4"></h4>
              <div className="card card-body bg-light mb-4">
                <div className="form-row">
                  <div className="col-12 col-md-6 col-xl-4 form-inline form-row mb-2">
                    <label className="col-12 col-sm-6 justify-content-end col-form-label">
                      Mail (kg.) :
                    </label>
                    <div className="col col-sm-6">
                      <Input
                        name="mail"
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.mail}
                        style={{ width: '100%' }}
                      />
                    </div>
                  </div>
                </div>
              </div>

              <div className="form-row">
                <div className="col-12 col-xl-6 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 col-xl-4 justify-content-end col-form-label">
                    Attach File :
                  </label>
                  <div className="col col-sm-6 col-xl">
                    <Upload {...props} style={{ width: '100%' }}>
                      <Button block>
                        <Icon type="upload" /> Click to Upload
                      </Button>
                    </Upload>
                  </div>
                </div>
                <div className="col-12 col-xl-6 form-inline form-row mb-2">
                  <label className="col-12 col-sm-6 col-xl-4 justify-content-end col-form-label">
                    Remark :
                  </label>
                  <div className="col col-sm-6 col-xl">
                    <TextArea
                      name="remark"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.remark}
                      rows={3}
                      style={{ width: '100%' }}
                    />
                  </div>
                </div>
              </div>
              <div className="text-center">
                <button
                  key="back"
                  disabled={this.props.disCancel}
                  onClick={this.props.onCancelClick}
                  type="button"
                  className="btn btn-light mr-2">
                  
                </button>
                <button
                  name="btnSendback"
                  key="sendback"
                  type="submit"
                  disabled={this.props.loadingSendback}
                  onClick={this.setTriggerBtnSendback}
                  className="btn btn-danger mr-2">
                  
                </button>
                <button
                  name="btnSubmit"
                  key="submit"
                  type="submit"
                  disabled={this.props.loadingOk}
                  onClick={this.setTriggerBtnSubmit}
                  className="btn btn-success mr-2">
                  
                </button>
                <button
                  name="btnApprove"
                  key="approve"
                  type="submit"
                  disabled={this.props.loadingApprove}
                  onClick={this.setTriggerBtnApprove}
                  className="btn btn-info mr-2">
                  
                </button>
              </div>
            </form>
          )}
        />
      </Fragment>
    )
  }
}
